


















import Vue from 'vue';

export default Vue.extend({
  name: 'Navbar',
  props: {},
  data() {
    return {
      isOpen: false,
    };
  },
  methods: {
    changeButton() {
      this.isOpen = !this.isOpen;
    },
    closeMenu() {
      this.isOpen = !this.isOpen;
    },
  },
  computed: {},
});
